@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Public Sans", sans-serif;
}

h1,
h2,
h4,
h5 {
  color: hsl(233, 26%, 24%);
  font-weight: 300;
}
h4 {
  font-weight: 700;
}
h5 {
  font-weight: 400;
  font-size: 20px;
}
h5:hover {
  color: hsl(136, 65%, 51%);
  cursor: pointer;
}
p {
  color: hsl(233, 8%, 62%);
  line-height: 1.5rem;
}
span {
  color: gray;
  font-size: 14px;
}

.NavBarLayout {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.NavBarMobile {
  text-align: center;
}
.NavBarMobile h1 {
  margin: 25px;
}
.Button {
  color: #ffffff;
  background: linear-gradient(0.25turn, hsl(136, 65%, 51%), hsl(192, 70%, 51%));
  padding: 10px 35px;
  border: none;
  border-radius: 21px;
  font-size: 14px;
  cursor: pointer;
  margin: 0;
}
.Button:focus {
  outline: none;
}



.Intro {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin: 50px auto;
}
.Intro img {
  width: 200px;
}
.IntroBackground {
  background-color: hsl(0, 0%, 98%);
  background-image: url(/Easybank-Landing-Page/static/media/bg-intro-desktop.8be308fd.svg);
  /* background-position: 450px -275px; */
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-evenly;
  padding: 100px 50px;
  height: 250px;
}
